import React from 'react'
import { FaArrowLeft } from 'react-icons/fa'
import { Link } from 'react-router-dom'


const Header = ({ onSave, info={ title: "" , description: "" }}) => {

    return (
        <header className="palette-collection-header">
            <div className="navigation">
                <div className="icon clickable-icon" >
                    <Link to="/palette-collection">
                        <div className="link-button">
                            <FaArrowLeft/> <div style={{ margin: "auto", padding: "0rem 0.5rem", whiteSpace:"nowrap", fontWeight:"bold" }}>Color Schemes</div>
                        </div>
                    </Link>
                </div>
            </div>
            <h1><input name="title-info" defaultValue={info.title} placeholder="Color palette name" onBlur={onSave}/></h1>
            <h2><input name="description-info" defaultValue={info.description} placeholder="Color palette description" onBlur={onSave}/></h2>
        </header>
    )
}

export default Header
